.present {
  // position: fixed;
  // top: 0;
  // z-index: 100;
  width: 100%;
  padding-top: 10.5px;
  padding-bottom: 10.5px;
  background-color: $c--sec;
  color: $txt-c--sec;
  overflow: hidden;
  // transform: translateY(-100%);
  transition: transform .5s ease;

  &.is-visible {
    transform: translateY(0);
  }

  &__inner {
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 1920px;
  }
  &__txt {
    padding: 6px 10px 6px 50px;
    margin: 0 auto;
    font-size: 1.8rem;
    line-height: to-em(20px, 1.8rem);
    background: url('../img/design/sprite-ff9761d8.svg#present') no-repeat 0 center / 30px 30px;
  }
  &__link {
    font-size: 1.3rem;
    line-height: to-em(16px, 1.3rem);
    font-family: $ff--sec;
  }
}
.close-btn {
  flex: 0 0 auto;
}
