  &__slider {

  }
}


.slider {
  position: relative;
  &__container {

  }

  &__slides-wrapper {

  }

  &__slide {

  }
  &__img {
    display: block;
    width: 100%;
    height: auto;
  }
  &__btn {
    position: absolute;
    top: 50%;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    border: 1px solid transparent;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, .6);
    cursor: pointer;
    transform: translateY(-50%);
  
    &:focus {
      outline: none;
      border-color: $c--prim;
    }    
  }

  &__btn--prev {
    left: 10px;

    @media screen and (min-width: 600px) {
      left: 20px;
    }
  }

  &__btn--next {
    right: 10px;
    
    @media screen and (min-width: 600px) {
      right: 20px;
    }
  }
}
