.main-page {

  & .header__bottom-row {
    position: relative;

    &::after {
      content: '';
      position: absolute;
      right: 0;
      top: 100%;
      display: block;
      width: 50px;
      height: 150px;
      background: url('../../img/design/sprite-ff9761d8.svg#flag') no-repeat 0 0 / 100% 100%;

      @media screen and (min-width: 900px) {
        width: 70px;
        height: 195px;
        background-size: 100% 100%;
      }
    }
  }

  .page__info-nums {
    @media screen and (max-width: 599px) {
      @include vis-hid;
    }
  }

  .page__info-garantee {
    @media screen and (max-width: 599px) {
      border-bottom: none;
    }
  }
}//.main-page