.link {
  display: flex;
  font-family: $ff--sec;
  text-decoration: none;
  color: $txt-c--colored-prim;

  @media screen and (max-width: 899px) {
    font-size: 1.3rem;
    line-height: to-em(18px, 1.3rem);
  }

  &.page__projects-link {

    @media screen and (max-width: 599px) {
      margin: 0 auto 40px;
      max-width: 300px;
      margin-right: auto;
      width: 100%;
      min-height: 50px;
      justify-content: center;
      align-items: center;      
      border: 1px solid;

      transition: 
        background-color .3s ease,
        color .3s ease;
    }

    &:hover {
      
      @media screen and (max-width: 599px) {
        background-color: $c--prim;
        color: $txt-c;
      }
    }

  }

  &:hover {
    color: darken($txt-c--colored-prim, 5%);
  }
}