.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor; 
  fill: currentColor;
  transition: fill .3s ease;
}

.icon-social {
  font-size: 3rem;
  fill: $c--prim;

  &:hover {
    fill: darken($c--prim, 10%);
  }
}

.icon-play-btn {
  margin-right: -2px;
  fill: $c--white;
}

.icon-slider-arrow {
  stroke: $c--white;
  stroke-width: 1px;
  transition: stroke .3s ease;
  
  .slider__btn:hover & {
    stroke: $c--prim;
  }

  .slider__btn--next & {
    transform: rotate(180deg);
  }
}

