.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  
  &__img {
    margin-bottom: -5px;
    flex: 0 1 auto;
    max-width: 100%;

    .header & {
      margin-right: 20px;
    }
  }

  &__txt {
    font-size: .8rem;
    line-height: to-em(12px, 0.8rem);
    max-width: 141px;

    @media screen and (min-width: 900px) {
      font-size: 1rem;
      line-height: to-em(13px, .9rem)
    }
  }
}
