$burger-w: 30px;
$burger-h: 22px;
$toggle-h: 2px; //Задавати тільки парне число

.burger {
  position: relative;
  z-index: 100;
  padding-left: 0;
  padding-right: 0;
  display: flex;
  align-items: center;
  flex: 0 0 $burger-w;
  height: $burger-h;
  border: none;
  background-color: transparent;
  transition: top .3s cubic-bezier(.59,.54,.47,.96);
  
  @media screen and (min-width: 1100px) {
    @include vis-hid; 
  }

  &__toggle {
    position: relative;
    display: inline-block;
    width: 100%;
    height: $toggle-h;
    background-color: $txt-c;
    transition: background-color 0s cubic-bezier(.59,.54,.47,.96) .3s;

    &::before, 
    &::after {
      content: '';
      position: absolute;
      left: 0;
      display: inline-block;
      width: inherit;
      height: inherit;
      background-color: $txt-c;
      transition: top .3s cubic-bezier(.59,.54,.47,.96) .3s,
      transform .3s cubic-bezier(.59,.54,.47,.96) 0s;


    }
    &::before {
      top: -(($burger-h - $toggle-h) / 2);
    }
    &::after {
      top: (($burger-h - $toggle-h) / 2);
    }

    .burger.is-active & {
      background-color: transparent;
      transition: background-color 0s cubic-bezier(.59,.54,.47,.96) .3s;

      &::before,
      &::after {
        top: 0;
        transition: top .3s cubic-bezier(.59,.54,.47,.96) 0s, 
        transform .3s cubic-bezier(.59,.54,.47,.96) .3s;
      }

      &::before {
        transform: rotate(-45deg);
      }
      &::after {
        transform: rotate(45deg);
      }
    }  
  }
}
.drop-content {
  @media screen and (max-width: 1099px) {

    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    background-color: #ececec;
    transform: translateX(100%);
    visibility: hidden;
    transition: 
      transform .3s ease-in-out,
      visibility .3s ease-in-out;
  
    &.is-visible {
      transform: translateX(0);
      visibility: visible;
    }

    &__inner {
      
      padding: 0 20px;
      height: 100%;
      overflow-y: auto;

      @media screen and (min-width: 900px) {
        @include scroll(.5em);

      }
    }
  }
}