  
.close-btn {
  position: relative;
  display: inline-block;
  width: 38px;
  height: 38px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  
  @media screen and (min-width: 900px) {
    width: 20px;
    height: 20px;
  }

  &::before, &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 2px;
    background-color: $c--white;
  }
  &::before {
    transform: translateY(-50%) rotate(45deg);
  }
  &::after {
    transform: translateY(-50%) rotate(-45deg);
  }

}