@mixin fontFace($name, $fileName, $weight, $style, $display) {

  @font-face {
    font-family: $name;
    font-style: $style;
    font-weight: $weight;
    src: url('../fonts/'+$fileName+'.woff2') format('woff2'),
         url('../fonts/'+$fileName+'.woff') format('woff');
    font-display: $display;
  }
}

@mixin vis-hid {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}


@mixin scroll($width) {

  &::-webkit-scrollbar {
    width: .2em;

    @media screen and (min-width: 1100px) {
      width: $width; 
    }
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  }

  &::-webkit-scrollbar-thumb {
    background-color: $c--prim;
    outline: 1px solid slategrey;
  }
}



@function rem-to-px($rem) {
  $res: ($rem / ($rem * 0 + 1)) * 10px;
  @return $res;
}

@function to-em($values, $fs) {
  
  $s: rem-to-px($fs);

  $list: ();//обявляю список
  
  @each $value in $values {
    $num: $value / $s;

    $list: append($list, $num * 1em);
  }  
  @return $list;
}

@mixin shadow {
  box-shadow: 0px 25px 10px -15px rgba(52, 55, 65, .5);
}