.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: $ff--sec;
  color: $txt-c--colored-prim;
  text-decoration: none;
  word-break: break-all;

  @media screen and (max-width: 899px) {
    font-size: 1.3rem;
    line-height: to-em(18px, 1.3rem);
  }
  @media screen and (min-width: 475px) {
    max-width: 200px;
  }

  &.main-menu__btn {

    @media screen and (min-width: 1100px) {
      justify-content: flex-end;     
      margin-top: -3px;
      font-size: 1.3rem;
      line-height: to-em(17px, 1.3rem);
    }
  
    .drop-content__inner > & {
      @extend .btn--fill;
      @extend .btn--fill-prim;
      @extend .btn--animated;
  
      margin-bottom: 30px;
      max-width: 100%;
    }
  }

  &.banner__btn {
    
    @media screen and (max-width: 599px) {
      @include vis-hid;
    }

  }
  
  &.services__btn {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
    transition:
      transform .4s ease, 
      opacity .4s ease,
      visibility .4s ease;

    @media screen and (max-width: 599px) {
      .services__item-inner:hover & {
        opacity: 0;
        visibility: hidden;
        transform: translateY(100%);
      }
    }
  }

  &.form__btn {
    margin-bottom: 10px;
  }
  &--fill {
    padding: .5em 1em;
    min-height: 50px;
    border: none;
  }
  &--fill-prim {
    background-color: $c--prim;
    color: $txt-c--sec;
    
    transition: background-color .3s ease-out;
    
    &:hover {
      background-color: darken($c--prim, 10%);
    }
  }
  
  &--fill-white {
    color: $txt-c--colored-prim;
    background-color: $c--white;

  }

  &--transparent {
    @extend .btn--fill;

    color: $txt-c--sec;
    border: 1px solid;
  }

  &--animated {
    position: relative;
    overflow: hidden;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -30%;
      width: 45px;
      height: 100%;
      background-image: linear-gradient(to right, transparent, rgba(255, 255, 255, .3));
      transform: skewX(-25deg);
      animation: move 3s ease-out infinite;
    
      @keyframes move {
        0% {
          left: -30%;
        }
        50% {
          left: 130%;
        }
        100% {
          left: 130%;
        }
      }
    }

  }
}