.contacts {
  font-size: 1.3rem;
  line-height: to-em(19px, 1.3rem);
  font-family: $ff--sec;

  .drop-content & {

    @media screen and (min-width: 1100px) {
      @include vis-hid;
    }
  }

  &__tel-list {
    @extend %list-reset;

    margin-bottom: 13px;
  }

  &__tel-item {

  }

  &__tel-link {
    text-decoration: none;
  }

  &__address {
    font-style: normal;

  }

  &__address-link {
    margin-bottom: 13px;
    display: inline-block;
    color: $txt-c--colored-prim;
    text-decoration: none;
  }

  &__address-txt {

  }

  &__social-list {
    @extend %list-reset;
    margin-left: -5px;
    margin-right: -5px;
    display: flex;
  }

  &__social-item {
    padding-left: 5px;
    padding-right: 5px;
  }

  &__social-link {

  }

}
