.menu {
    display: flex;
    font-family: $ff--sec;

    .drop-content__inner & {
      margin-bottom: 20px;
    }
    // так як елемент пригаючий display: none;
    //не дае йому шириини і висоти при побудові сторінки
    .header__top-row & {
      @media screen and (max-width: 1099px) {
        display: none;
      }
    }

  &__list {
    @extend %list-reset;

    display: flex;
    flex-flow: column nowrap;

    @media screen and (min-width: 1100px) {
      flex-flow: row nowrap;
      align-items: center;
    }
  }

  &__item {

    &:not(:last-child) {

      @media screen and (min-width: 1100px) {
        margin-right: 25px;
      }
    }
  }

  &__link {
    position: relative;
    display: block;
    font-size: 1.3rem;
    line-height: to-em(15px, 1.3rem);
    text-decoration: none;

    @media screen and (max-width: 1099px) {
      padding: to-em(10px 0px, 1.3rem);
      color: $txt-c--colored-prim;
    }
    @media screen and (min-width: 900px) {
      padding: to-em(5px 0px, 1.3rem);
    }

    &::after {
      
      @media screen and (min-width: 1100px) {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        display: block;
        width: 100%;
        height: 1px;
        background-color: $c--white;
        transform: scaleX(0);
        transition: transform .3s ease-out;
      }
    }

    &:hover::after {
      @media screen and (min-width: 1100px) {
        transform: scaleX(1);
      }
    }
  }
}