$container-width: 1170px;
$grid-gutter-width: 40px;

.container {
  margin: 0 auto;
  padding-left: $grid-gutter-width / 2;
  padding-right: $grid-gutter-width / 2;
  // .container width ...px + padding 20px *2
  max-width: $container-width + $grid-gutter-width;
  height: 100%;
}

.l-grid {
  display: flex;
  flex-flow: row wrap;
}
@mixin grid {
  display: flex;
}

@mixin grid-item($columns) {
  flex: 0 1 percentage($columns / 12);
}