.page {

  &__baner {
    margin-bottom: 20px;
    min-height: 320px;
    background:
      linear-gradient(rgba(0, 0, 0, .6), rgba(0, 0, 0, .6)) no-repeat 0 0 / cover,
      url('../img/design/overlay@0.5.jpg') no-repeat 50% 50% / cover;

    @media screen and (min-width: 600px) {
      @include shadow;

      margin: 0 20px 65px 20px;
      background-image: 
        linear-gradient(rgba(0, 0, 0, .4), rgba(0, 0, 0, .4)),
        url('../img/design/overlay.jpg');
    }

    @media screen and (min-width: 900px) {
      min-height: 520px;
    }

    @media screen and (min-width: 1200px) {
      background-image: 
        linear-gradient(rgba(0, 0, 0, .4), rgba(0, 0, 0, .4)),
        url('../img/design/overlay@2x.jpg');
    }

  }

  &__baner-inner {
    padding-bottom: 35px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    min-height: inherit;
    color: $txt-c--sec;
    overflow-wrap: break-word;
    
    @media screen and (max-width: 599px) {
      padding-right: 40px;
    }

    @media screen and (min-width: 900px) {
      padding-top: 0;
      justify-content: center;
    }
  }

  &__baner-title {
    margin-bottom: to-em(5px, 3.4rem);

    @media screen and (min-width: 900px) {
      margin-bottom: to-em(10px, 5.4rem);
      padding-top: 37px;
    }
  }

  &__baner-desc {
    font-family: $ff--sec;
    @media screen and (max-width: 599px) {
      margin-bottom: 0;
    }

    @media screen and (min-width: 900px) {
      margin-bottom: to-em(23px, 2.4rem);
      font-size: 2.4rem;
      line-height: to-em(27px, 2.4rem);
    }
  }

  &__info {
    margin-bottom: 40px;

    @media screen and (min-width: 900px) {
      margin-bottom: 107px;
    }
  }

  &__info-more {
    @media screen and (max-width: 599px) {
      margin-bottom: 35px;
    }

    @media screen and (min-width: 600px) {
      @include vis-hid;
    }
  }

  &__info-garantee {
    border-bottom: 1px solid rgba(0, 0, 0, .1);

    @media screen and (max-width: 599px) {
      margin-bottom: 45px;
    }

    @media screen and (min-width: 600px) {
      padding-bottom: 25px;
    }
    @media screen and (min-width: 900px) {
      padding-bottom: 55px;
    }
  }

  &__info-title {
    margin-bottom: to-em(15px, 1.5rem);
    font-size: 1.5rem;
    line-height: to-em(16px, 1.5rem);
    font-family: $ff--sec;
    color: $txt-c--colored-sec;


    @media screen and (min-width: 600px) {
      margin-bottom: to-em(20px, 1.8rem);
      font-size: 1.8rem;
      line-height: 1em;
    }
  }

  &__info-list {
    @extend %list-reset;
    
    display: flex;
    flex-flow: column wrap;

    @media screen and (min-width: 600px) {
      flex-flow: row wrap;
      justify-content: space-between;
    }
  }

  &__info-item {
    display: flex;
    flex-flow: column;
    justify-content: center;
    font-size: 1.8rem;
    line-height: to-em(20px, 1.8rem);

    @media screen and (max-width: 599px) {
      padding-left: 10px;
      min-height: 30px;
      border-left: 1px solid $c--sec;
    }
    
    &:not(:last-child) {
      @media screen and (max-width: 599px) {
        margin-bottom: to-em(13px, 1.5rem);
      }
    }
    
    @media screen and (min-width: 600px) {
      justify-content: flex-start;
      margin-bottom: to-em(15px, 2.2rem);
      flex: 0 1 calc(50% - 15px);
      font-size: 2.2rem;
      line-height: to-em(25px, 2.2rem);
    }

    @media screen and (min-width: 900px) {
      margin-bottom: 0;
      padding-right: 5px;
      flex: 0 0 23%;
    }
  }

  &__info-nums {
    
    @media screen and (min-width: 600px) {
      margin-bottom: 45px;
      padding-top: 35px;
    }

    @media screen and (min-width: 900px) {
      padding-top: 45px;

    }
  }

  &__info-num {
      font-size: 2.2rem;
      line-height: 1em;

    @media screen and (min-width: 600px) {
      font-size: 3.2rem;
      line-height: 1em;
    }

    @media screen and (min-width: 900px) {
      margin-bottom: 3px;
    }
    &--blue {
      color: $txt-c--colored-prim;
    }
  }
}
.services {
  display: flex;
  flex-flow: column;
  color: $txt-c--sec;

  @media screen and (min-width: 600px) {
    margin-left: -15px;
    margin-right: -15px;
    flex-flow: row nowrap;
  }

  &__item {

    &:not(:last-child) {
      @media screen and (max-width: 599px) {
        margin-bottom: 20px;
      }
    }
    @media screen and (min-width: 600px) {
      padding-right: 15px;
      padding-left: 15px;
      flex: 1 1 50%;
    }
  }
  &__item-inner {
    @include shadow;

    position: relative;
    padding: 20px;

    @media screen and (min-width: 900px) {
      padding: 30px 30px 40px;
    }

    &::before,
    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      width: 100%;
      height: 100%;
      transition: filter .4s ease;
    }
    &::before {
      z-index: -1;
      filter: blur(0px) saturate(1.2);
    }
    &::after {
      z-index: -1;
      background-color: transparent;
      transition: background-color .4s ease;
    }

    &:hover::after {
      background-color: rgba(0, 0, 0, 0.2);

    }

    &:hover::before {
      filter: blur(4px);
      transition: filter .2s ease;
    }
  }

  &__design-inner {
    &::before {
      background: url('../img/design/design-mob.jpg') no-repeat 50% 50% / cover rgba(0, 0, 0, .1);
    
      @media screen and (min-width: 900px) {
        background: url('../img/design/design.jpg') no-repeat 50% 50% / cover rgba(0, 0, 0, .1);
      }

      @media screen and (min-width: 1200px) {
        background: url('../img/design/design@2x.jpg') no-repeat 50% 50% / cover rgba(0, 0, 0, .1);
      }
    }
  }

  &__repairs-inner {
    &::before {
      background: url('../img/design/repairs-mob.jpg') no-repeat 50% 50% / cover rgba(0, 0, 0, .1);

      @media screen and (min-width: 900px) {
        background: url('../img/design/repairs.jpg') no-repeat 50% 50% / cover rgba(0, 0, 0, .1);
      }

      @media screen and (min-width: 1200px) {
        background: url('../img/design/repairs@2x.jpg') no-repeat 50% 50% / cover rgba(0, 0, 0, .1);
      }
    }
  }

  &__title {
    font-size: 3.2rem;
    line-height: 1em;
  }

  &__txt {
    margin-bottom: 25px;
    font-size: 1.8rem;
    line-height: to-em(28px, 1.8rem);
    transform: translateY(-50%);
    opacity: 0;
    visibility: hidden;
    transition: 
      transform .4s ease,
      opacity .4s ease,
      visibility .4s ease;

    .services__item-inner:hover & {
      transform: translateY(70px);
      opacity: 1;
      visibility: visible;

      @media screen and (min-width: 600px) {
        transform: translateY(0);
      }
    }

    & ul {
      @extend %list-reset;
    }
  }

}

.technology {
  margin-bottom: 40px;

  @media screen and (min-width: 900px) {
    margin-bottom: 103px;
  }
  
  &__header {
    margin-bottom: 20px;

    @media screen and (min-width: 600px) {
      display: flex;
      flex-flow: row wrap;
    }

    @media screen and (min-width: 900px) {
      margin-bottom: 25px;
    }
  }

  &__title {
    margin-bottom: to-em(14px, 2.4rem);
    
    
    @media screen and (min-width: 600px) {
      flex: 0 1 100%;
    }
  }

  &__tags-list {
    @extend %list-reset;
    
    margin-right: auto;
    display: flex;
    flex-flow: row wrap;
    align-items: flex-start;
    flex: .28 1 auto;
  }

  &__tags-item {
    font-family: $ff--sec;
    padding: to-em(10px 10px 10px 55px, 1.5rem);
    background: url('../img/design/sprite-ff9761d8.svg#clock2') no-repeat 0 0 / 40px 40px;
    flex: 1 1 auto;

    &:nth-child(2) {
      background-image: url('../img/design/sprite-ff9761d8.svg#p');
    }
    &:not(:last-child) {
      @media screen and (max-width: 599px) {
        margin-bottom: 10px;
      }
    }
  }
  &__link {
    @media screen and (max-width: 599px) {
      @include vis-hid;
    }

    @media screen and (min-width: 600px) {
      align-self: center;
      // flex: 1 1 auto;
    }
      
    @media screen and (min-width: 900px) {
      text-align: right
    }
  }

  &__previews {
    margin-bottom: 40px;
    display: flex;
    flex-flow: column nowrap;

    @media screen and (min-width: 600px) {
      margin-right: -15px;
      margin-left: -15px;
      flex-flow: row nowrap;
      justify-content: center;
    }
  } 

  &__previews-item {
    min-width: 0;
    padding-bottom: 25px;

    @media screen and (min-width: 600px) {
      padding: 0 15px 0 15px;
      flex: 0 1 33.333%;
    }

    &:not(:first-child) {

      @media screen and (max-width: 599px) {
        padding-top: 25px;
      }
    }
    &:not(:last-child) {
      border-bottom: 1px solid rgba(0, 0, 0, .1);

      @media screen and (min-width: 600px) {
        border-bottom: none;
        border-right: 1px solid rgba(0, 0, 0, .1);
      }
    }
  }

  &__item-inner {
    display: flex;
    flex-flow: column nowrap;
    height: 100%;

  }

  &__video {
    position: relative;
    margin-bottom: 15px;
    padding-top: percentage(9/16);

    @media screen and (min-width: 900px) {
      margin-bottom: 17px;
    }

    & video {
      @include shadow;

      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__video-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 0;
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 50%;
    background-color: transparent;
    transform: translate(-50%, -50%);
    cursor: pointer;

    &::after, &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: $c--prim;
    }

    &::before {
      animation: pulse 2s ease-in-out infinite;
    }

    &::after {
    }
  }

  &__previews-title {
    margin-bottom: to-em(10px, 2.4rem);
    font-size: 2.4rem;
    line-height: to-em(25px, 2.4rem);
    

    @media screen and (min-width: 900px) {
      margin-bottom: to-em(15px, 3rem);
      font-size: 3rem;
      line-height: to-em(32px, 3rem);
    }
  }

  &__previews-txt {
    font-size: 1.3rem;
    line-height: to-em(15px, 1.3rem);
    font-family: $ff--sec;
    max-height: 100px;
    overflow: hidden;

    @media screen and (min-width: 600px) {
      margin-bottom: to-em(15px, 1.5rem);
      font-size: 1.5rem;
      line-height: to-em(23px, 1.5rem);
    }

    p {
      overflow-wrap: break-word;
    }
    p:last-child {
      @extend %text-overflow;

      margin-bottom: 0;
    }
  }

  &__previews-link {
    margin-top: auto;

    @media screen and (max-width: 599px) {
      @include vis-hid;
    }
  }
}


.svg {
  &__line {
      stroke: $c--prim;
  }
  &__circle {
      fill: $c--prim;
  }
}

.steps {
  margin-bottom: 50px;

  @media screen and (min-width: 900px) {
    margin-bottom: 139px;
  }

  &__title {
    margin-bottom: 1.1em;
  }

  &__draw-area {
    position: relative;
  }
  &__svg-wrap {
    position: absolute;
    z-index: -1;
    top: 0;
    left: -15px;
    right: -15px;
    bottom: 0;
  }

  &__list {
    @extend %list-reset;
    
    margin-left: -15px;
    margin-right: -15px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    
    @media screen and (max-width: 599px) {
      margin-bottom: 20px;
    }
  }

  &__item {
    display: flex;
    margin-bottom: 10px;
    padding-left: 15px;
    padding-right: 15px;

    flex: 1 1 100%;
    min-width: 0;
    opacity: 0;
    transform: translateY(30%);

    .steps__list.is-visible & {
      opacity: 1;
      transform: translateY(0);
      transition: opacity .3s cubic-bezier(.59,.54,.47,.96),
                  transform .5s cubic-bezier(.59,.54,.47,.96);
    }

    @media screen and (min-width: 600px) {
      margin-bottom: 30px;
      flex: 0 1 50%;
    }

    @media screen and (min-width: 900px) {
      flex: 0 1 33.333%;
  
    }

    &:last-child {
      @media screen and (max-width: 599px) {
        margin-bottom: 0;
      }
    }

  }

  &__item-inner {
    flex: 1 1 100%;
    position: relative;
    padding: 87px 20px 24px;
    border: 1px solid rgba(0, 0, 0, .1);
    background-position: 20px 30px;
    background-repeat: no-repeat;
    background-size: 40px 40px;
    background-color: #fff;

    .steps__item:nth-child(1) & {
      background-image: url('../img/design/sprite-ff9761d8.svg#stage-ic1');
    }
    .steps__item:nth-child(2) & {
      background-image: url('../img/design/sprite-ff9761d8.svg#stage-ic2');
    }
    .steps__item:nth-child(3) & {
      background-image: url('../img/design/sprite-ff9761d8.svg#stage-ic3');
    }
    .steps__item:nth-child(4) & {
      background-image: url('../img/design/sprite-ff9761d8.svg#stage-ic4');
    }
    .steps__item:nth-child(5) & {
      background-image: url('../img/design/sprite-ff9761d8.svg#stage-ic5');
    }
    .steps__item:nth-child(6) & {
      background-image: url('../img/design/sprite-ff9761d8.svg#stage-ic6');
    }
    
    &--fill {
      background-color: $c--sec;
    }
  }

  &__item-num {
    position: absolute;
    top: 23px;
    right: 20px;
    float: right;
    color: lighten($txt-c, 40%);
  }

  &__item-title {
    margin-bottom: to-em(5px, 1.8rem);
    color: darken($txt-c, 40%);
  }

  &__item-txt {
    font-family: $ff--sec;

    @media screen and (max-width: 599px) {
      font-size: 1.3rem;
      line-height: to-em(19px, 1.3rem);
    }
    p {
      overflow-wrap: break-word;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.page {

  &__projects {
    padding-top: 40px;
    color: $txt-c--sec;
    background-color: darken($c--dark, 5%);
  
    @media screen and (min-width: 900px) {
      padding-top: 76px;
    }
  }

  &__projects-header {
    margin-bottom: 25px;
    display: flex;
    
    @media screen and (min-width: 600px) {
      margin-bottom: 38px;
      justify-content: space-between;
      align-items: baseline;
    }
  }

  &__projects-title {
    margin-bottom: 0;
  }

  &__projects-gallery {

  }

  &__our-team {

  }
}
.projects-gallery {

  @media screen and (min-width: 600px) {
    margin-left: -15px;
    margin-right: -15px;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
  }

  &__item {
    position: relative;

    @media screen and (min-width: 600px) {
      padding-left: 15px;
      padding-right: 15px;
      width: 50%;
    }
  }

  &__slider {
    margin-bottom: 13px;

    @media screen and (min-width: 600px) {
      margin-bottom: 24px;
    }
  }

  &__label {
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 1;
    padding: to-em(8px 20px, .9rem);
    border-radius: 20px;
    font-size: .9rem;
    line-height: to-em(12px, .9rem);
    font-family: $ff--sec;
    background-color: rgba(0, 0, 0, .6);
    
    @media screen and (min-width: 600px) {
      left: 35px;// if min-width: 600px i add margin-left/right -15px 
      top: 20px;
    }
  }

  &__desc {
    margin-bottom: to-em(55px, 1.5rem);
    font-family: $ff--sec;

    @media screen and (max-width: 599px) {
      margin-bottom: to-em(35px, 1.3rem);
      font-size: 1.3rem;
      line-height: to-em(19px, 1.3rem);
    }
  }
}

.feedback {
  background-color: $c--prim;
  @media screen and (max-width: 599px) {
    margin-left: -20px;
    margin-right: -20px;
  }
  @media screen and (min-width: 600px) {
    background: url('../img/design/form-bg.jpg') no-repeat 100% 0 / contain $c--prim;
  }
  &__inner {
    padding-left: 20px;
    padding-right: 20px;
  }

  &__title {
    font-size: 2.4rem;
    line-height: to-em(26px, 2.4rem);

    @media screen and (min-width: 600px) {
      font-size: 3.2rem;;
      line-height: to-em(36px, 3.2rem);
    }


  }

  &__form {

  }

  &__form-policy {

  }
}
