.main-menu {

  @media screen and (max-width: 1099px) {
    margin-bottom: 20px;
    border-top: 1px solid rgba(0, 0, 0, .1);
  }


  &__list {
    @extend %list-reset;

    display: flex;
    flex-flow: column;

    @media screen and (min-width: 1100px) {
      align-items: center;
      flex-flow: row nowrap;
    }
  }

  &__item {

    @media screen and (min-width: 1100px) {
      position: relative;
      display: flex;
      justify-content: center;
      // flex: 2 1 auto;
      flex-grow: 1;
    }

    &:last-child {
      flex-grow: 0.4;
    }

    &:not(:last-child)::after {
      @media screen and (min-width: 1100px) {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        display: block;
        width: 1px;
        height: 30px;
        background-color: rgba(0, 0, 0, .1);
        transform: translateY(-50%);
      }
    }
  }

  &__work {
    flex-grow: 1.3;
  }

  &__inner-list {
    @extend %list-reset;

    display: flex;
    
    @media screen and (max-width: 1099px) {
      flex-flow: column nowrap;
    }
  }
  &__inner-item {
    &:not(:last-child) {
      @media screen and (min-width: 1100px) {
        margin-right: 15px;
      }
    }
  }

  &__link {
    padding: to-em(5px, 1.8rem);
    display: block;
    font-size: 1.8rem;
    line-height: to-em(21px, 1.8rem);
    text-decoration: none;
    transition: color .3s ease;

    @media screen and (max-width: 1099px) {
      padding: to-em(19px 0px, 1.8rem);
      border-bottom: 1px solid rgba(0, 0, 0, .1);
    }

    &:hover {

      @media screen and (min-width: 1100px) {
        color: $txt-c--colored-sec;
      }
    }
  }

  &__work-link {

    @media screen and (min-width: 1100px) {
      position: relative;
      padding-left: 20px;
    }

    &::before, &::after {
      @media screen and (min-width: 1100px) {
        content: '';
        position: absolute;
        left: 0;
        top: calc(50% - 5px);
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: $c--sec;
      }
    }
    
    &::before {

        animation: pulse 1.5s ease-in-out infinite;
        
        @keyframes pulse {
          from {
            transform: scale(1);
            opacity: .7;
          }
          to {
            transform: scale(2);
            opacity: 0;
          }
        }
    }  

    & sup {
      @media screen and (max-width: 1099px) {
        @include vis-hid;
      }

      @media screen and (min-width: 1100px) {
        color: $txt-c--colored-sec;
      }
    }
  }

  &__repair-calc {
    @media screen and (max-width: 1099px) {
      display: none;
    }

  }

  &__tel {

    @media screen and (min-width: 1100px) {
      margin-left: auto;
      display: flex;
      flex-flow: column;
      justify-content: center;
      align-items: flex-end;
      transition: color .3s ease;
    }

    &:hover {
      color: $txt-c--colored-sec;
    }
  }

  &__tel-link {
    @media screen and (max-width: 599px) {
      width: 30px;
      height: 30px;
      background-size: 24px 24px;
      padding: 0;
    }
    @media screen and (max-width: 1099px) {
      z-index: 100;
      margin-left: auto;
      margin-right: 15px;
      padding-left: 25px;
      font-weight: 600;
      background: url('../img/design/sprite-ff9761d8.svg#phone-mob') no-repeat 0 center / 19px 19px;
    }
    @media screen and (min-width: 600px) {
      font-size: 2rem;
      line-height: to-em(23px, 2rem);
      text-decoration: none;
    }
  }
  &__tel-num {
    @media screen and (max-width: 599px) {
      @include vis-hid;
    }
  }
}