.header {
  // position: fixed;
  // top: 0;
  // z-index: 100;
  width: 100%;
  background-color: $c--white;
  box-shadow: 0 7px 16px rgba(0, 0, 0, .15);
  transition: top .3s ease;

  @media screen and (min-width: 600px) {
    margin-bottom: 20px;
  }
  
  @media screen and (min-width: 900px) {
    margin-bottom: 30px;
  }

  &__top-row {
    color: $txt-c--sec;
    background-color: $c--prim;
    
    @media screen and (min-width: 1100px) {
      padding-top: 7px;
      padding-bottom: 7px;
    }
  }
  &__bottom-row {
    min-height: 64px;
    
    @media screen and (max-width: 1099px) {
      justify-content: space-between;
    }
  }
  
  &__inner {
    display: flex;
    align-items: center;

    .header__top-row & {
      justify-content: space-between;
    }

  }

  &__work-time {
    @media screen and (max-width: 1099px) {
      @include vis-hid;
    }

    @media screen and (min-width: 1100px) {
      padding: to-em(5px 0px 5px 30px, 1.2rem);
      font-size: 1.2rem;
      line-height: to-em(14px, 1.2rem);
      font-family: $ff--sec;
      background: url('../img/design/sprite-ff9761d8.svg#clock1') no-repeat 0 center / 20px 20px; 
    }

  }

  &__main-menu-wrap {
    @media screen and (min-width: 1100px) {
      flex: 1 1 auto;
    }
  }
}
