
html {
  box-sizing: border-box;
  height: 100%;
  font-size: 10px;

}

*, *::before, *::after {
  box-sizing: border-box;
  margin-top: 0;
}
body {
  height: 100%;
  font-size: 1.5rem;
  line-height: 1.5em;
  font-family: $ff--prim;
  color: $txt-c;
  background-color: $c--white;
  transition: transform .4s ease-out;

/*  @media screen and (min-width: 900px) {
    @include scroll(.5em);
  }*/

}
a {
  color: inherit;
}

[class$="__txt"] > p {
  &:last-of-type {
    margin-bottom: 0;
    overflow-wrap: break-word;
  }
}


h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
  margin-top: 0;
  overflow-wrap: break-word;
}

h1 {
  font-size: 3.4rem;
  line-height: to-em(35px, 3.4rem);
  font-weight: normal;

  @media screen and (min-width: 900px) {
    font-size: 5.4rem;
    line-height: to-em(57px, 5.4rem);
  }
}

h2 {
  font-size: 2.4rem;
  line-height: to-em(27px, 2.4rem);

  @media screen and (min-width: 900px) {
    font-size: 3rem;
    line-height: to-em(33px, 3rem);
  }
}

h3 {
  font-size: 1.8rem;
  line-height: to-em(22px, 1.8rem);

  @media screen and (min-width: 900px) {
    font-size: 2.2rem;
    line-height: to-em(26px, 2.2rem);
  }
}

video {
  max-width: 100%;
}

.scroll-off {
  overflow: hidden;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .9);
  visibility: hidden;
  opacity: 0;
  transition: 
    opacity .5s ease-in-out,
    visibility .5s ease;

  &.is-visible {
    visibility: visible;
    opacity: 1;
  }
}

%list-reset {
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

%text-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
} 
