.input-field {
  padding: to-em(0px 20px, 1.3rem);
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 50px;
  border: 1px solid;
  border-top: none;
  border-bottom: none;
  // font-size: 1.3rem;
  // line-height: to-em(18px, 1.3rem);
  // font-family: $ff--sec;
  // color: $txt-c--sec;
  background-color: transparent;

  @media screen and (min-width: 475px) {
    max-width: 285px;
  }
  @media screen and (min-width: 600px) {
    font-size: 1.4rem;
    line-height: to-em(18px, 1.4rem);
  }

  &:focus {
    outline: none;
    border: 1px solid;
  }
}