.about-page {

  &__inner {
    display: flex;
  }
  &__sidebar {
    @media screen and (max-width: 899px) {
      @include vis-hid;
    }

    @media screen and (min-width: 900px) {
      @include grid-item(3);
    }

  }
  &__body {
    @include grid-item(12);

    @media screen and (min-width: 900px) {
      @include grid-item(9);

    }

  }

  .services {
    @media screen and (min-width: 600px) {
      flex-flow: row wrap;
    }

    &__item {

      @media screen and (min-width: 600px) {
        flex: 1 1 100%;
      }
      
      &:not(:last-child) {
        margin-bottom: 20px;

        @media screen and (min-width: 900px) {
          margin-bottom: 40px;
        }
      }
    }
  }

  & .page {

    &__info-garantee {
      margin-bottom: 0;
      padding-bottom: 35px;
    }

    &__info-nums {
      padding-top: 35px;
    }

    &__info-item {
      @media screen and (min-width: 600px) {
        flex: 0 1 calc(33% - 10px);
      }
    }

    &__info-item:last-child {
        @media screen and (min-width: 600px) {
          @include vis-hid;
        }
    }
  }


}